<template>
    <div class="list-h">
        <header class="header">
            <div class="container">
                <span @click="btnGo" class="btnGo"></span>
                <div class="input-main fl"><input v-model="$store.state.search_value" type="search" @keyup.enter="searchData" placeholder="请输入您需要的产品!"></div>
                <div class="btn-ng clearfix" @click="addClass">
                    <span class="fl"></span>
                    <span class="fl"></span>
                    <span class="fl"></span>
                </div>
                <transition  name="fade">
                    <div class="box-wrap" v-if="sta">
                        <div class="box-bg" @click="addClass"></div>
                        <div class="bomb-box">
                            <div class="polygon"></div>
                            <div class="btn" @click="toLink('/home')">
                                <span class="icon"><i></i></span>首页
                            </div>
                            <div class="btn">
                                <span class="icon" @click="toLink('/myNews')"><i></i></span>消息
                            </div>
                        </div>
                    </div>
                </transition>
            </div>     
        </header>
    </div>
    </template>
<script>
    import qs from 'qs';
    import { MessageBox,Toast } from 'mint-ui';
    export default {
        name : 'heade',
        data(){
            return {
                sta:false,
                ruleCon:null
            }
        },
        methods:{
             toLink(link){
                this.$router.push({
                    path:link
                });
            },
            addClass(){
                if(this.sta == false){
                    this.sta = true;
                }else{
                    this.sta = false;
                }
            },
            searchData(){
                this.$store.state.roll_switch = true;
                this.$store.state.no_data_sw = false;
                this.$store.state.load_show = true;
                this.$store.state.sliding_data_sw = false;
                this.$store.state.search_data = [];
                this.$store.state.sort_page = 1;
                this.$store.state.slide_switch = false;
                 this.$store.commit('changeRecruitScrollY','');
                if((/^\s*$/.test(this.$store.state.search_value)))return;
                this.axios.get(API_URL + 'Home/Index/keyWordSearch',{
                    params:{
                            keyword:this.$store.state.search_value,
                            page:this.$store.state.sort_page
                        }
                }).then((res) => {
                    if(res.data.status == 1){
                        this.$store.state.load_show = true;
                         //第一次获取数据如果数据没铺满屏幕 不再触发滚动
                        if(res.data.data.length <10 && this.$store.state.sort_page == 1){
                            this.$store.state.roll_switch = false;
                            this.$store.state.load_show = false;
                        }
                        for (let i = 0; i < res.data.data.length; i++) {
                            this.$store.state.search_data.push(res.data.data[i]);
                        }
                        this.$store.state.slide_switch = true;
                    }else{
                         this.$store.state.roll_switch = false;
                         this.$store.state.sliding_data_sw = false;
                         this.$store.state.load_show =false;
                         this.$store.state.no_data_sw = true;
                    }
                    this.$store.state.sort_status = null;
                    this.$store.state.sort_id = null;
                }).catch((err) => {
                    console.info('FailtrueErr', err);
                });
            },
            btnGo(){
                this.$router.go(-1);
            },
        },
    }
</script>
<style lang="less" scoped>
    .list-h{
        height: .94rem;
    }
    .header {
        position: fixed;
        z-index:9999;
        top:0;
        left:0;
        width: 100%;
        height: .62rem;
        background: #fff;
        padding: .16rem 0;
        text-align:center;
        color:#fff;
        font-size:.36rem;
        font-weight:bold;
        line-height:.62rem;
        .container{
            height:100%;
            position:relative;
            top:0;
            left:0;
            .input-main {
                width: 5.4rem;
                height: 100%;
                border-radius: 100px;
                background: url(../../../assets/nav_search.png) no-repeat .24rem center;
                background-size: .35rem .35rem;
                background-color: #f7f7f7;
                line-height:100%;
                margin-left:1rem;
                input {
                    height: 100%;
                    width: 100%;
                    border: none;
                    background: none;
                    text-indent: .7rem;
                    font-size: .26rem;
                    color: #999;
                    outline: none;
                }
                input::-webkit-input-placeholder, textarea::-webkit-input-placeholder { 
                    color: #999; 
                } 
                input:-moz-placeholder, textarea:-moz-placeholder { 
                    color: #999; 
                } 
                input::-moz-placeholder, textarea::-moz-placeholder { 
                    color: #999; 
                } 
                input:-ms-input-placeholder, textarea:-ms-input-placeholder { 
                    color: #999; 
                } 
            }
            .btn-ng{
                position:absolute;
                top:0;
                right:.2rem;
                height: 100%;
                span{
                    display:block;
                    width:.12rem;
                    height:.12rem;
                    border:.06rem solid #5A5A5A;
                    border-radius:50%;
                    box-sizing:border-box;
                    background:#fff;
                    margin-left:.1rem;
                    margin-top:.25rem;
                }
            }
            .box-wrap{
            position:fixed;
            width: 100%;
            height: 100%;
            left:0;
            top:0;
            z-index:99;
            .box-bg{
                position:absolute;
                left:0;
                top:0;
                width: 100%;
                height: 100%;
                z-index:1;
            }
            .bomb-box{
                background:#535353;
                padding:.11rem;
                box-sizing: border-box;
                position: absolute;
                right:.15rem;
                top:.8rem;
                border-radius:5px;
                z-index:2;
                .polygon{
                    width: .4rem;
                    height: .4rem;
                    position:absolute;
                    top:0;
                    left:.8rem;
                    transform: rotate(45deg);
                    background:#535353;
                }
                .btn:nth-child(2){
                    border-bottom:1px solid #666;
                    .icon{
                        i{
                            width: .24rem;
                            height: .24rem;
                            background:url(../../../assets/home.png) no-repeat;
                            background-size:100% 100%;
                        }
                    }
                }
                .btn:nth-child(3){
                    border-top:1px solid #444;
                    .icon{
                        i{
                            width: .24rem;
                            height: .24rem;
                            background:url(../../../assets/news1.png) no-repeat;
                            background-size:100% 100%;
                        }
                    }
                }
                .btn{
                    width: 1.1rem;
                    height: .56rem;
                    box-sizing: border-box;
                    text-align:center;
                    line-height: .56rem;
                    position:relative;
                    padding-left:.5rem;
                    font-size:.24rem;
                    .icon{
                        position:absolute;
                        left:0;
                        top:0;
                        width: .56rem;
                        height: 100%;
                        margin:0;
                        border:none;
                        background:none;
                        i{
                            position:absolute;
                            left:0%;
                            top:0%;
                            bottom:0;
                            right:0;
                            margin:auto;
                        }
                    }
                }
            }
        }
            .btnGo{
                position:absolute;
                transform:rotate(360deg);
                -ms-transform:rotate(180deg); 	/* IE 9 */
                -moz-transform:rotate(180deg); 	/* Firefox */
                -webkit-transform:rotate(180deg); /* Safari 和 Chrome */
                -o-transform:rotate(180deg); 	/* Opera */
                left:.2rem;
                top:50%;
                margin-top:-.23rem;
                width:.46rem;
                height:.46rem;
                background:url(../../../assets/btn-return005.png) no-repeat;
                background-size:100% 100%;
            }
            .cartBtn{
                position:absolute;
                right:.3rem;
                top:50%;
                margin-top:-.3rem;
                width:.55rem;
                height:.55rem;
                background:url(../../../assets/nav_cart.png) no-repeat;
                background-size:100% 100%;
            }
        }
        
    }
</style>


