<template>
    <div>
        <div v-title data-title="商品列表">商品列表</div>
        <list-header></list-header>
        <ul class="sort-wrap clearfix text-center">
            <li class="fl" :class="{active:$store.state.sort_status == 1}" @click="sort('xl')">销量
                <span class="icon">
                                <em class="top" :class="{active:$store.state.sort_id == 1}"></em><br>
                                <em class="bottom" :class="{active:$store.state.sort_id == 2}"></em>
                            </span>
            </li>
            <li class="fl" :class="{active:$store.state.sort_status == 2}" @click="sort('jg')">价格
                <span class="icon">
                                <em class="top" :class="{active:$store.state.sort_id == 3}"></em><br>
                                <em class="bottom" :class="{active:$store.state.sort_id == 4}"></em>
                            </span>
            </li>
            <li class="fl" @click="sort('rq')" :class="{active:$store.state.sort_status == 3}">人气</li>
        </ul>
        <ul class="z-list-wrap" ref="list">
            <li class="clearfix" v-for="(item,index) in $store.state.search_data" :key="item.id" @click="tolink(index)">
                <img v-lazy="URL + item.img" class="fl">
                <div class="list-text fl">
                    <p class="text"><span v-if="item.goods_marking == i" class="mark" v-for="(m,i) in mark" :key="m.id">{{m}}</span>{{item.title}}</p>
                    <p class="price-market">￥ {{item.price_market}}</p>
                    <p class="new-price">￥
                        <span>{{item.price_member}}</span>
                    </p>
                </div>
            </li>
        </ul>
        <div v-if="$store.state.no_data_sw" class="comm-null">
            <div class="con-wrap text-center">
                <img src="../../assets/null_com.png">
                <p>暂无商品</p>
            </div>
        </div>
        <div class="up-warp" v-show="$store.state.load_show">
            <p class="rotate"></p>
            <p class="load-title">加载中..</p>
        </div>
        <div class="no-data" v-show="$store.state.sliding_data_sw">暂无更多数据~</div>
        <Shopsn></Shopsn>
        <to-Top></to-Top>
    </div>
</template>
    
<script>
    import listHeader from '@/components/page/children/list-header.vue';
    import qs from 'qs';
    import toTop from '@/components/superMarket/children/toTop';
    import Shopsn from '@/components/page/Shopsn.vue';
    export default {
        name: 'list',
        data() {
            return {
                logoImg: require('@/assets/btn-return.png'),
                status: [true, false, false],
                search: true,
                scrollWatch: true,
                api_url: '',
                limit: false,
                mark: {},
                position: false, //保存的位置只赋一次值
                num:''
            }
        },
        updated() {
            this.$nextTick(function() {
                this.position = this.$store.state.recruitScrollY ? false : true;
                if (this.position === true) return;
                document.body.scrollTop = this.$store.state.recruitScrollY;
            })
        },
        beforeRouteLeave(to, from, next) {
            let position = this.getScrollTop(); //记录离开页面的位置
            if (position == null) position = 0;
            this.$store.state.listData = this.$store.state.search_data;
            this.$store.commit('changeRecruitScrollY', position); //离开路由时把位置存起来
            this.$store.state.listInfo = {
                sort_id: this.$store.state.sort_id,
                search_value: this.$store.state.search_value
            };
            next()
        },
        mounted() {
            window.addEventListener('scroll', this.getDataList); 
        },
        destroyed() {
			window.removeEventListener('scroll', this.getDataList);
		},
        methods: {
            getDataList() {
                if (document.body.scrollTop + window.innerHeight >= document.documentElement.scrollHeight) {
                    //如果第一次请求没数据或数据没达到每页个数就不会再请求数据
                    if (this.$store.state.roll_switch == false) {
                        return;
                    } else {
                        this.$store.state.load_show = true;
                    };
                    //滑动开关避免多次滑动请求
                    if (this.$store.state.slide_switch == true) {
                        this.$store.state.slide_switch = false;
                        this.$store.state.sort_page++;
                        this.sort();
                    }
    
                }
            },
            getScrollTop() { //获取滚动条
                var scroll_top = 0;
                if (document.documentElement && document.documentElement.scrollTop) {
                    scroll_top = document.documentElement.scrollTop;
                } else if (document.body) {
                    scroll_top = document.body.scrollTop;
                }
                return scroll_top;
            },
            getMark() {
                if (this.num === '') {
                    this.num = '1-999'
                }
                this.axios.post(API_URL + 'Home/Integral/integral_goods',qs.stringify({
                    num:this.num
                })).then((res) => {
                    this.load_wrap = false;
                    this.data = res.data.data;
                    console.log(this.data);
                    if(res.data.status == 0) return;
                    this.integral = res.data.data[0].integral;
                }).catch((err) => {
                    console.log(err);
                });
            },
            //请求成功后的操作
            returnOperation(data) {
                this.$store.state.load_show = true;
                //第一次获取数据如果数据没铺满屏幕 不再触发滚动
                if (data.length < 10 && this.$store.state.sort_page == 1) {
                    this.$store.state.load_show = false; //滑动动画隐藏
                    this.$store.state.roll_switch = false; //第一次请求没数据或数据没达到每页个数开关
                }
                for (let index = 0; index < data.length; index++) {
                    this.$store.state.search_data.push(data[index]);
                }
                this.$store.state.slide_switch = true;
            },
            toback: function() {
                this.$router.go(-1);
            },
            addClass: function(index) {
                for (var i = 0; i < this.status.length; i++) {
                    this.$set(this.status, i, false);
                }
                this.$set(this.status, index, true);
            },
            sort(index) {
                if (this.limit === true) {
                    return
                }; //限制多次请求
                this.limit = true;
                this.$store.state.slide_switch = false;
                switch (index) {
                    case 'xl':
                        this.$store.state.roll_switch = true;
                        this.$store.state.search_data = [];
                        this.$store.state.sort_page = 1;
                        if (this.$store.state.sort_id !== 1) {
                            this.$store.state.sort_id = 1;
                            this.$store.state.sort_status = 1;
                        } else {
                            this.$store.state.sort_id = 2;
                            this.$store.state.sort_status = 1;
                        }
                        break;
                    case 'jg':
                        this.$store.state.roll_switch = true;
                        this.$store.state.search_data = [];
                        this.$store.state.sort_page = 1;
                        if (this.$store.state.sort_id != 3) {
                            this.$store.state.sort_id = 3;
                            this.$store.state.sort_status = 2;
                        } else {
                            this.$store.state.sort_id = 4;
                            this.$store.state.sort_status = 2;
                        }
                        break;
                    case 'rq':
                        this.$store.state.roll_switch = true;
                        this.$store.state.search_data = [];
                        this.$store.state.sort_page = 1;
                        // if(this.$store.state.sort_id == 5)return;;
                        this.$store.state.sort_id = 5;
                        this.$store.state.sort_status = 3;
                        break;
                }
                this.$store.state.load_show = true;
                this.$store.state.sliding_data_sw = false;
                this.$store.state.no_data_sw = false;
                if (!this.$store.state.search_value == '') {
                    this.api_url = API_URL + 'Home/Index/keyWordSearch';
                } else {
                    this.judgeUrl();
                }
                if (!this.$store.state.search_value == '' || this.$route.params.status == 'search') {
                    let data = '';
                    if (this.$store.state.search_value) {
                        data = this.$store.state.search_value;
                    } else {
                        data = this.$route.params.id;
                    }
                    this.axios.get(this.api_url, {
                        params: {
                            keyword: data,
                            app_user_id: sessionStorage.getItem('user_ID'),
                            sort: this.$store.state.sort_id,
                            page: this.$store.state.sort_page
                        }
                    }).then((res) => {
                        this.$store.commit('changeRecruitScrollY', '');
                        this.limit = false;
                        if (res.data.status == 1) {
                            this.returnOperation(res.data.data);
                        } else if (res.data.status == 0 && this.$store.state.sort_page == 1) {
                            this.$store.state.no_data_sw = true;
                            this.$store.state.sliding_data_sw = false;
                            this.$store.state.load_show = false;
                            this.$store.state.roll_switch = false;
                        } else {
                            this.$store.state.load_show = false;
                            this.$store.state.sliding_data_sw = true;
                            this.$store.state.roll_switch = false;
    
                        }
                    }).catch((err) => {
                        console.info('FailtrueErr', err);
                    });
                } else if (this.$route.params.status == 'nav') {
                    this.axios.get(this.api_url, {
                        params: {
                            sort: this.$store.state.sort_id,
                            class_id: this.$route.params.id,
                            page: this.$store.state.sort_page,
                            app_user_id: sessionStorage.getItem('user_ID')
                        }
                    }).then((res) => {
                        this.$store.commit('changeRecruitScrollY', '');
                        this.limit = false;
                        if (res.data.status == 1) {
                            this.returnOperation(res.data.data);
                        } else if (res.data.status == 0 && this.$store.state.sort_page == 1) {
                            this.$store.state.no_data_sw = true;
                            this.$store.state.sliding_data_sw = false;
                            this.$store.state.load_show = false;
                            this.$store.state.roll_switch = false;
                        } else {
                            this.$store.state.load_show = false;
                            this.$store.state.sliding_data_sw = true;
                            this.$store.state.roll_switch = false;
    
                        }
                    }).catch((err) => {
                        console.info('FailtrueErr', err);
                    });
                } else {
                    this.axios.get(this.api_url, {
                        params: {
                            sort: this.$store.state.sort_id,
                            class_id: this.$route.params.status,
                            page: this.$store.state.sort_page,
                            app_user_id: sessionStorage.getItem('user_ID')
                        }
                    }).then((res) => {
                        this.$store.commit('changeRecruitScrollY', '');
                        this.limit = false;
                        if (res.data.status == 1) {
                            this.returnOperation(res.data.data);
                        } else if (res.data.status == 0 && this.$store.state.sort_page == 1) {
                            this.$store.state.no_data_sw = true;
                            this.$store.state.sliding_data_sw = false;
                            this.$store.state.load_show = false;
                            this.$store.state.roll_switch = false;
                        } else {
                            this.$store.state.load_show = false;
                            this.$store.state.sliding_data_sw = true;
                            this.$store.state.roll_switch = false;
                        }
                    }).catch((err) => {
                        console.info('FailtrueErr', err);
                    });
                }
            },
            tolink(index) {
                this.$router.push({
                    name: 'product',
                    params: {
                        id: this.$store.state.search_data[index].id,
                        status: 1
                    }
                });
            },
            judgeUrl() {
                switch (this.$route.params.status) {
                    case 'search':
                        this.api_url = API_URL + 'Home/Index/keyWordSearch';
                        break;
                    case 'nav':
                        this.api_url = API_URL + 'Home/Supermarket/getGoodsbyClass';
                        break;
                    default:
                        this.api_url = API_URL + 'Home/Goods/getProduct';
                }
            },
            initialization() {
                if (this.$store.state.listData.length > 0) {
                    this.$store.state.search_data = this.$store.state.listData;
                    if (this.$store.state.listInfo) {
                        let listInfo = this.$store.state.listInfo;
                        this.$store.state.sort_id = listInfo.sort_id;
                        this.$store.state.search_value = listInfo.search_value;
                    }
                } else {
                    this.$store.state.sort_id = null;
                    this.$store.state.search_value = '';
                    // this.position = this.$route.name === 'list' ?true : false;
                    this.$store.state.search_data = [];
                    this.$store.state.sort_status = null;
                    this.$store.state.sort_page = 1;
                    this.$store.state.roll_switch = true;
                    this.$store.state.slide_switch = false;
                    this.$store.state.load_show = true;
                    this.$store.state.no_data_sw = false;
                    this.sort();
                }
    
            }
        },
        created() {
            this.initialization();
            this.getMark();
        },
        components: {
            listHeader,
            Shopsn,
            toTop
        }
    }
</script>

<style lang="less" scoped>
    .comm-null {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #fff;
        padding-top: 2.1rem;
        p {
            font-size: .28rem;
            color: #666;
            padding-top: .2rem;
        }
    }
    
    .sort-wrap {
        background-image: -webkit-linear-gradient(top, #d0d0d0, #d0d0d0 50%, transparent 50%);
        background-image: linear-gradient(180deg, #d0d0d0, #d0d0d0 50%, transparent 50%);
        background-size: 120% 1px;
        background-repeat: no-repeat;
        background-position: bottom left;
        background-origin: content-box;
        height: .9rem;
    }
    
    .sort-wrap {
        line-height: .9rem;
        position: relative;
        z-index: 999;
        li {
            font-family: 'NotoSansHans-Regular';
            width: 33.3333333%;
            font-size: .32rem;
            color: #1f1f1f;
            font-weight: 400;
            position: relative;
            .icon {
                position: absolute;
                left: 1.6rem;
                top: 0;
                height: 100%;
                .top {
                    position: absolute;
                    left: 0;
                    top: .33rem;
                    width: .2rem;
                    height: .12rem;
                    background: url(../../assets/sort-top.png);
                    background-size: 100% 100%;
                }
                .top.active {
                    background: url(../../assets/sort-top-active.png);
                    background-size: 100% 100%;
                }
                .bottom {
                    position: absolute;
                    left: 0;
                    width: .2rem;
                    height: .15rem;
                    bottom: .33rem;
                    background: url(../../assets/sort-bottom.png);
                    background-size: 100% 100%;
                }
                .bottom.active {
                    background: url(../../assets/sort-bottom-active.png);
                    background-size: 100% 100%;
                }
            }
        }
        li.active {
            color: #f81919;
        }
    }
    
    .z-list-wrap {
        background: #fff;
        li {
            height: 2.1rem;
            padding: 0 .13rem;
            background-image: -webkit-linear-gradient(top, #d0d0d0, #d0d0d0 50%, transparent 50%);
            background-image: linear-gradient(180deg, #d0d0d0, #d0d0d0 50%, transparent 50%);
            background-size: 120% 1px;
            background-repeat: no-repeat;
            background-position: bottom left;
            background-origin: content-box;
            img {
                width: 2rem;
                height: 2rem;
                border-radius: .15rem;
            }
            .list-text {
                width: 4.3rem;
                padding-left: .25rem;
                .text {
                    font-family: 'NotoSansHans-Regular';
                    font-size: .3rem;
                    color: #333;
                    line-height: .45rem;
                    padding-top: .1rem;
                    height: .9rem;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    .mark {
                        position: relative;
                        padding: 0 .08rem;
                        display: inline-block;
                        color: #f81919;
                        line-height: normal;
                        font-size: .2rem;
                        vertical-align: middle;
                        margin-top: -3px;
                    }
                    
                    .mark::after {
                        content: "";
                        display: block;
                        border: 1px solid #f81919;
                        border-radius: 25px;
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        left: 0;
                        right: 0;
                        pointer-events: none;
                    }
                    
                    @media only screen and (-webkit-min-device-pixel-ratio: 2) {
                        .mark::after {
                        right: -100%;
                        bottom: -100%;
                        -webkit-transform: scale(.5);
                        -webkit-transform-origin: 0 0;
                        }
                    }
                }
                .price-market {
                    text-decoration: line-through;
                    font-size: .26rem;
                    color: #aeadad;
                    padding-top: .2rem;
                }
                .new-price {
                    font-size: .32rem;
                    color: #f23030;
                    padding-top: .1rem;
                    span {
                        font-size: .32rem;
                        font-weight: 600;
                    }
                }
            }
        }
    }
    
    .up-warp {
        height: .5rem;
        padding: .3rem 0;
        text-align: center;
        p {
            display: inline-block;
            vertical-align: middle;
        }
        .rotate {
            width: 16px;
            height: 16px;
            border-radius: 50%;
            border: 1px solid gray;
            margin-right: 6px;
            border-bottom-color: transparent;
        }
        .rotate {
            -webkit-animation: rotate .6s linear infinite;
            animation: rotate .6s linear infinite
        }
        @-webkit-keyframes rotate {
            0% {
                -webkit-transform: rotate(0deg)
            }
            100% {
                -webkit-transform: rotate(360deg)
            }
        }
        @keyframes rotate {
            0% {
                transform: rotate(0deg)
            }
            100% {
                transform: rotate(360deg)
            }
        }
        .load-title {
            font-size: 12px;
            color: gray;
        }
    }
    
    .no-data {
        background-color: #fff;
        height: .5rem;
        padding: .3rem 0;
        text-align: center;
        font-size: 12px;
        color: gray;
    }
    
    .status {
        background: #fff;
        text-align: center;
        padding-bottom: .2rem;
        font-size: .3rem;
        color: #333;
    }
</style>

<style lang="less">
    .load {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 999999;
    }
    
    .load .mint-spinner-triple-bounce {
        width: 100%;
        text-align: center;
        position: absolute;
        left: 0;
        top: 50%;
    }
</style>


